import React, {useState, useEffect} from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

export default function SupplierTable() {

  const [item, setItem] = useState([]);
  useEffect(() => {
    fetch("http://localhost:8080/Materials/includes/view_all_suppliers.inc.php")
    .then(res => res.json())
    .then(
        (result) => {
          setItem(result);
        }
      )
  }, [])

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 700 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Supplier Name and Address</StyledTableCell>
            <StyledTableCell align="right">Vendor Type</StyledTableCell>
            <StyledTableCell align="right">PS/BD/Germany</StyledTableCell>
            <StyledTableCell align="right">Criticality</StyledTableCell>
            <StyledTableCell align="right">Materials</StyledTableCell>
            <StyledTableCell align="right">Material Type</StyledTableCell>
            <StyledTableCell align="right">ISO 9001 exp.</StyledTableCell>
            <StyledTableCell align="right">Other Cert</StyledTableCell>
            <StyledTableCell align="right">QAA</StyledTableCell>
            <StyledTableCell align="right">Quest.</StyledTableCell>
          </TableRow>
        <TableBody>
          {item.map((row) => (
            <StyledTableRow key={row.Supplier_Name}>
              <StyledTableCell component="th" scope="row">{row.Supplier_Name}</StyledTableCell>
              <StyledTableCell align="right">{row.Vendor_Type}</StyledTableCell>
              <StyledTableCell align="right">{row.PS_BD_Germ}</StyledTableCell>
              <StyledTableCell align="right">{row.Criticality}</StyledTableCell>
              <StyledTableCell align="right">{row.Mat}</StyledTableCell>
              <StyledTableCell align="right">{row.Mat_Type}</StyledTableCell>
              <StyledTableCell align="right">{row.iso}</StyledTableCell>
              <StyledTableCell align="right">{row.certi}</StyledTableCell>
              <StyledTableCell align="right">{row.QAA}</StyledTableCell>
              <StyledTableCell align="right">{row.Quest}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
        </TableHead>
      </Table>
    </TableContainer>
  );
}
